<template>
	<div class="searchBar">
		<input type="text" class="isbntext" name="" v-model="isbn" @change="searchISBN"/>
		<input type="file" accept="image/*" capture="camera" @change="changetoISBN"/>
		<button @click="searchISBN">search</button>
	</div>
</template>
<script>
	export default{
		data (){
			return {
				url: `https://api.douban.com/v2/book/isbn/`,
				isbn:"",
				state: {
					inputStream: {
						size: 800,
						singleChannel: false
					},
					locator: {
						patchSize: "medium",
						halfSample: true
					},
					decoder: {
						readers: [{
							format: "ean_reader",
							config: {}
						}]
					},
					locate: true,
					src: ""
				},
				newBook:{

				}
			}
		},
		mounted: function(){

		},
		filters:{

		},
		methods:{
			changetoISBN: function(el){
				var src= URL.createObjectURL(el.target.files[0]);
				this.state.src= src;
				var self = this;
				Quagga.decodeSingle(this.state, function(result){
					if(result.codeResult){
						self.isbn = result.codeResult.code;
						// self.searchLocal();
						self.searchDouban();
					}else{
						alert("");
					}
				});
				this.state.src = "";

			},
			searchISBN: function(el){
				if(!this.isbn){
					el.target.parentNode.children[0].focus();
				}

			},
			searchLocal: function(){

			},
			searchDouban: function(){
				if(!this.isbn){
					return ;
				}
				var url = this.url+this.isbn;
				this.$http.jsonp(url).then(response=>{
					var res = response.body;
					console.log(res);
					this.handlebook(res);
				});
			},
			handlebook(res){
				this.$store.commit('takeJson', res);
			},
		}
	}
</script>
<style type="text/css">
	.searchBar{
		display: flex;
		width: 100%;
	}
	.isbntext:focus{
		border: 1px solid #C40000;
		outline: none;
	}
</style>


