<template>
	<ul class="nvlist">
		<li v-for="book in bookList">
		<img v-bind:src="book.img" v-bind:alt="book.bookName">
		<div class="detail">
			<div class="title">{{book.bookName}}</div>
			<div class="pages">{{Math.floor(book.nowPage/book.totalPage*100)}}%</div>
			<a class="start" @click="startToRead(book)"></a>
		</div>	
		</li>
		<button @click="readLocalData">more</button>
	</ul>
</template>

<script>
	export default{
		data (){
			return {
				bookList:[],
			}
		},
		mounted: function(){
			this.$nextTick(function(){
				var _this = this;
				_this.readLocalData();
			})
		},
		filters:{

		},
		methods:{
			readLocalData: function(){
				this.$http.get("./data/localbook.json").then(response=>{
					var res = response.body;
					this.bookList = res.result;
				});
			},
			startToRead: function(book){

			},
		}
	}
</script>

<style type="text/css">
	.nvlist {
		margin: 10px 10px;
		width: 90%;
	}
	.nvlist li {
		margin: 10px;
		width: 100%;
		display: flex;
	}
	img {
		border: 1px solid #eee;
		flex:1;
	}
	.detail{
		flex: 3;
	}
</style>


