<template>
<div class="bkdetail">
	<div class="up">
		<img v-bind:src="detail.images.medium">
		<div class="publish">
<!-- 			<p>{{detail.title}}-{{detail.subtitle}}</p> -->
			<p>{{detail.author}}</p>
			<p>{{detail.publisher}}</p>
			<p>{{detail.pubdate}}</p>
			<p>{{detail.pages}}</p>
			<p>{{detail.price}}</p>
			<p></p>
		</div>
	</div>
	<div class="readingmsg">
		
	</div>
	<div class="about">
		{{detail.summary}}
	</div>
</div>
</template>
<script>
	export default{
		data(){
			return {
				detail: this.$store.state.bkdetail
			}
		},
		mounted: function(){
			this.$nextTick(function(){
				this.setDetail();
			})
		},
		filters:{

		},
		methods:{
			setDetail: function(){
				var that = this;
				this.$http.get("./data/detail.json").then(response=>{
					var res = response.body;
					console.log(res);
					that.$store.commit('takeJson', res.result[0]);
				});
			},
			putLog: function(){
				console.log(this.detail);
			},
		}
	}
</script>
<style type="text/css">
.bkdetail{
	width: 100%;

}
	.up {
		width: 100%;

		display: flex;
	}
	img{
		flex:1;
	}
	.publish{
		flex: 3;
	}
	.about{
		margin: 10px 10px;
		width: 90%;
		font-size: 0.6rem;
		text-align: left;
	}
	p{
		text-align: left;
		font-size: 0.6rem;
		margin: 10px 10px;
	}
</style>


